<template>
	<!-- Main Area -->
	<div class="main-area">
		<div class="bg-overly" :style="{ backgroundImage: `url('${albumDetails.picture}')` }"></div>
		<section class="track-section">
			<div class="track-section-title">
				<h3>{{albumDetails.title}}</h3>
				<button class="btn" @click="playAll()">Play All</button>
			</div>
			<div class="track-items">
				<div class="track-item pointer" v-for="(track, index) in albumDetails.tracks" :index="index" :key="track.id"
				:class="{ active: currentTrackId == track.slug }">
					<div class="track-footer" @click="addAndPlayTrack(track)">
						<span class="track-index-container">
							<div v-if="index >= 9">{{index + 1}}</div>
							<div v-else>{{'0' + (index + 1)}}</div>
							
							<span class="track-index">
								<span class="iconify" data-icon="el:play" data-inline="false"></span>
							</span>
							<span class="track-index track-index-play-pause">
								<span class="iconify" data-icon="simple-line-icons:energy" data-inline="false"></span>
							</span>
						</span>
						<h4>{{track.title}}</h4>
						<span>{{track.album}}</span>
						<span v-for="(artist, index) in track.artists" :key="index">{{artist}}</span>
					</div>
					<div class="track-control">
						<span 
							class="iconify" 
							data-icon="simple-line-icons:options"
						></span>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
import { bus } from '@/main';
const axios = require('axios');
export default {
	name: 'Album',
	data: function(){
		return{
			albumDetails: [],
		}
	},
	computed: {
		currentTrackId: function(){
			return this.$store.state.currentTrackId
		}
	},
	methods: {
		addQueue: function(e){
			this.$store.dispatch('addQueue', e)
		},
		addAndPlayTrack: function(e){
			this.$store.dispatch('addQueue', e);
			this.$store.dispatch('selectTrack', e.slug);
			bus.$emit('scrollToActive');
		},
		playAll: function(){
			this.$store.dispatch('playAll', this.albumDetails.tracks);
			this.$store.dispatch('selectTrack', this.albumDetails.tracks[0].slug);
			bus.$emit('scrollToActive');
		}
	},
	mounted(){
		axios.get('/api/albums/' + this.$route.params.slug + '/?format=json').then(response => (this.albumDetails = response.data))
	},
	watch:{
		$route (){
			axios.get('/api/albums/' + this.$route.params.slug + '/?format=json').then(response => (this.albumDetails = response.data))
		}
	}
}
</script>

<style scoped lang="scss">
$font1: 'Ubuntu Mono', monospace;

/* Color */
$black: rgb(20, 20, 20);
$white: #fff;

@mixin transition($time){
    -o-transition: ease-in-out all $time;
    -moz-transition: ease-in-out all $time;
    -webkit-transition: ease-in-out all $time;
    transition: ease-in-out all $time;
}

@mixin respond-to($breakpoint) {
    @if $breakpoint=="xs" {
        @media (max-width: 567px) {
            @content;
        }
    };
    @if $breakpoint=="sm" {
        @media (max-width: 767px) {
            @content;
        }
    };
    @if $breakpoint=="md" {
        @media (max-width: 991px) {
            @content;
        }
    };
    @if $breakpoint=="lg" {
        @media (max-width: 1200px) {
            @content;
        }
    };
}
.bg-overly {
	position: fixed;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    opacity: .1;
    filter: blur(13px);
    top: 0;
    left: 0;
	z-index: -1;
}
.track-section{
	margin-top: 25px;
	margin-bottom: 25px;
	.track-section-title{
		margin-bottom: 30px;
		display: flex;
		align-items: center;
		h3{
			font-size: 24px;
			font-weight: 700;
			margin-right: 15px;
			margin-top: 10px;
		}
	}

	.track-items{
		display: block;
		.track-item{
			display: flex;
			border-top: 1px solid;
			padding: 0 15px;
			position: relative;
			&:hover{
				background-color: rgba($black, .1);
				.track-footer{
					.track-index{
						display: block;
						&.track-index-play-pause{
							display: none;
						}
					}
				}
			}
			&.active{
				.track-footer{
					.track-index{
						display: none;
						&.track-index-play-pause{
							display: block;
						}
					}
				}
			}
			.track-control{
				width: 25px;
				height: 39px;
				display: flex;
				align-items: center;
				justify-content: center;
				.iconify{
					top: 1px;
				}
			}
			.track-footer{
				display: grid;
				grid-template-columns: 50px 2fr 1.5fr .5fr;
				padding: 7px 0;
				width: calc(100% - 25px);
				h4{
					font-size: 16px;
					font-weight: 700;
					margin-bottom: 0;
					opacity: .9;
					line-height: 22px;
				}
				span{
					opacity: .6;
					.track-index{
						padding: 10px;
						width: 40px;
						height: 39px;
						position: absolute;
						top: 0;
						left: 0;
						background-color: $black;
						display: flex;
						justify-content: center;
						align-items: center;
						opacity: 1;
						display: none;
						>*{
							position: relative;
							top: -3px;
							left: 3px;
							color: $white;
						}
					}
				}
			}
			&:last-child{
				border-bottom: 1px solid;
			}
		}
	}
}
</style>
